import React, { lazy, useEffect, useState } from 'react'
import { AnimationWrapper } from 'react-hover-animation'
import {
  CBadge,
  CButton,
  CInput,
  CModal,
  CAlert,
  CButtonGroup,
  CLink,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout,
  CDataTable,
  CCollapse,
  CModalHeader,
  CModalBody,
  CModalFooter
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {fieldviavalen} from './source.js'

const ViaValenDataDemand = (props) => {
  const [tambah, setTambah] = useState("")
  const [data, setData] = useState([
    {
      id:1,
      noaju:"4000077EEF8F61512202100001",
      rincianpenggunaan:"Rumah Tangga",
      bulan:"Februari",
      jenis:"Garam Industri",
      waktu:"05/03/2021 07:00:16",
      pembuat:"PMO Kemenko",
      periode:"2020",
    }   
  ])
  const handleTambah = (angka) => {
    setTambah(1)
  }
  const [details,setDetails] = useState([])
  const toggleDetails = (index) =>{
    const posisi = details.indexOf(index)
    let newDetails = details.slice()
    if(posisi !== -1){
      newDetails.splice(posisi, 1)
    } else {
      newDetails = [...details, index]
    }
    setDetails(newDetails)
  }
  useEffect(() => {
    if(tambah == 1){
        props.history.push('/rakortas/form')
    } 
  }); 
  const [modalrincianpenggunaan, setModalrincianpenggunaan] = useState(false)
  const [modalhistori, setModalhistori] = useState(false)
  return (
    <>
     {/* <CAlert color="warning" closeButton>
        <h6>SIMULASI USER KEMENKO</h6>
      </CAlert>      */}
    <CModal
    show={modalhistori}
    onClose={()=>setModalhistori(false)}
    size="md"
    >
      <CModalHeader closeButton>
        <h4>Masukkan PIN</h4>
      </CModalHeader>
      <CModalBody>
        <CRow>
            <CCol md="12">
                <CInput type="password"/>
            </CCol>
        </CRow>        
      </CModalBody>
      <CModalFooter>
            <CRow>
                <CButton onClick={()=>setModalhistori(false)} className="btn btn-danger">Tetapkan</CButton>
            </CRow>        
      </CModalFooter>
    </CModal>
    <CModal
    show={modalrincianpenggunaan}
    onClose={()=>setModalrincianpenggunaan(false)}
    >
      <CModalHeader closeButton>
        <h4>Pilih Form</h4>
      </CModalHeader>
      <CModalBody>
      <AnimationWrapper>
      <CCard color="success" className="text-white text-center">
            <CCardBody>
            <CLink to='/kl/formdemand'class="stretched-link"></CLink>
              <blockquote className="card-bodyquote">
                <h5>Rincian Rencana Kebutuhan Rumah Tangga atau Komersial</h5>
              </blockquote>
            </CCardBody>
          </CCard>
      </AnimationWrapper>
      {/* <AnimationWrapper>
      <CCard color="info" className="text-white text-center">
            <CCardBody>
            <CLink to='/kl/formdemand'class="stretched-link"></CLink>
              <blockquote className="card-bodyquote">
                <h4>Rincian Rencana Kebutuhan Komersial</h4>
              </blockquote>
            </CCardBody>
          </CCard> 
      </AnimationWrapper> */}
      <AnimationWrapper>
      <CCard color="primary" className="text-white text-center">
            <CCardBody>
            <CLink to='/kl/formsupply'class="stretched-link"></CLink>
              <blockquote className="card-bodyquote">
                <h5>Rincian Rencana Produksi Tambak Garam</h5>
              </blockquote>
            </CCardBody>
          </CCard>  
      </AnimationWrapper>                               
      </CModalBody>
    </CModal>
    <CCard>
      <CCardHeader>
        <h4>Data Keputusan Rakortas</h4>
      </CCardHeader>
      <CCardBody>
        <CDataTable
          fields={fieldviavalen}
          items={data}
          addTableClasses="josss"
          overTableSlot={<div className="d-flex justify-content-end pb-2"><CButton onClick={()=>handleTambah()} color="info"> Tambah + </CButton></div>}   
          scopedSlots = {{
            'action':
              (item)=>{
                var angka
                if(item.rincianpenggunaan == "Rumah Tangga"){
                  angka = 1
                } else {
                  angka = 2
                }
                return(
                  <td>
                      <CButton onClick={()=>setModalhistori(true)} size="sm" color="success" >Tetapkan</CButton>{' '}
                      <CButton size="sm" color="info">Edit</CButton>{' '}                      
                      <CButton size="sm" color="danger">Hapus</CButton>
                  </td>                  
                )
              },
              'no':
              (item)=>(
                <td>
                    {item.id}
                </td>
              ),
              'status':
              (item, index)=>(
                <td>
                  <CButton onClick={()=>{toggleDetails(index)}}>
                  {details.includes(index) ? 
                  <CIcon size={'sm'} name={'cilArrowCircleBottom'} /> 
                  :
                  <CIcon size={'sm'} name={'cilArrowCircleRight'} />
                  }  
                  </CButton>{' '}{item.status}
                </td>
              ),
              'details':
              (item, index)=>(
                <CCollapse show={details.includes(index)}>
                  <table>
                  {item.status == "Draft"
                  ?
                  <tr>
                    <td style={{width:750}}>
                    </td>
                    <td style={{width:200}}>
                      - Draft                  
                    </td>
                    <td style={{width:400}}>
                    04/03/2021 20:48:29
                    </td>                    
                  </tr>   
                  :
                  <>
                  <tr>
                    <td style={{width:750}}>
                    </td>
                    <td style={{width:200}}>
                      - Draft                  
                    </td>
                    <td style={{width:400}}>
                    04/03/2021 20:48:29
                    </td>                    
                  </tr>
                  <tr>
                    <td style={{width:750}}>
                    </td>
                    <td style={{width:200}}>
                      - Terkirim                  
                    </td>
                    <td style={{width:400}}>
                    04/03/2021 20:48:29
                    </td>                    
                  </tr>                                       
                  </>                                 
                  }
                  </table>
                </CCollapse>
              )                                         
          }}
        />    
      </CCardBody>
    </CCard>
    </>
  )
}

export default ViaValenDataDemand
